import request from "@/utils/request";


async function sendOtp({ requestId, phoneNumber }) {
  const res = await request.post(
    `/customer-screening/otp/sms/send?requestId=${requestId}&phoneNumber=${phoneNumber}`,
    {}
  );
  let answer = "СМС не отправлена -";
  if (res.status < 400) {
    answer = "СМС отправлен !";
  }
  console.log(answer);
}


async function otpVerify({ requestId, phoneNumber, otp }) {
  const res = await request.post(
    `/customer-screening/otp/sms/verify?requestId=${requestId}&phoneNumber=${phoneNumber}&otp=${otp}`,
    {}
  );
  const text = await res.text();
  if (text.indexOf('SUCCESS')>=0) {
    console.log("СМС подтвержден !");
    return true;
  }
  console.log("СМС не подтвержден -");
  return false;
}


export { sendOtp, otpVerify };
