import config from "config";

const toString = (query) => {
	const toJSON = (param) =>
		typeof param === `object` ? JSON.stringify(param) : param;
	const urify = (param) => toJSON(param);
	return Object.keys(query)
		.map((key) => `${urify(key)}=${encodeURIComponent(urify(query[key]))}`)
		.join(`&`);
};

const request = (method) => (endpoint, params) => {
	
	let uri = `${config.apiUrl}${endpoint}`;

	const opts = {
		headers: {},
		method,
	};

	opts.headers = {"Content-Type": `application/json; charset=utf-8` } 

	if (/get|delete/.test(method) && params) {
		if (Object.entries(params).length > 0) {
			uri = `${config.apiUrl}${endpoint}?${toString(params)}`;
		}
	}

	if (/PATCH|POST|PUT/.test(method)) {
		opts.body = JSON.stringify(params);
	}
	return fetch(uri, opts).then((res) => {
		return res;
	});
};

export default {
	get: request(`get`),
	patch: request(`PATCH`),
	post: request(`POST`),
	put: request(`PUT`),
	delete: request(`delete`),
};
